*{
    box-sizing: border-box;
}

body{
    background-color: #333;
    color: darkkhaki;
    margin: 0px;
    font-family: 'Courier New', Courier, monospace;
    text-align: center;
}
.drum-pad{
    background-color: #4caf4f00; /* Green */
    border: 1px solid #dadada;
    color: #dadada;
    padding: 5px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
}
.drum-pad:focus{
    background-color: rgb(163, 118, 33);
    border: 1px solid #dadada;
    color: #dadada;
    padding: 5px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    cursor: pointer;
}