.box {
    margin: auto;
    margin-top: 200px;
    width: 550px;
    border: 1px solid #cacaca;
    display: flex;
}
.leftside {
    display: grid;
    grid-gap: 40px;
    grid-template-columns: repeat(3, 1fr);
    flex-grow: 2;
    margin: 10px;
}
.rightside{
    flex-grow: 1;
    display: flex;
    padding: 15px;
    border-left: 1px solid white;
    flex-direction: column;
    justify-content: space-between;
}
.flex{
    display: flex;
    flex-direction: column;
}